body {
  margin-top: 20px;
}

.section {
  padding: 100px 0;
  position: relative;
}

img {
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
}

.about-text h3 {
  margin: 0 0 6px;
  font-size: 45px;
  font-weight: 700;
}

@media (width <= 767px) {
  .about-text h3 {
    font-size: 35px;
  }
}

.about-text h6 {
  margin-bottom: 15px;
  font-weight: 600;
}

@media (width <= 767px) {
  .about-text h6 {
    font-size: 18px;
  }
}

.about-text p {
  max-width: 450px;
  font-size: 18px;
}

.about-text p mark {
  color: #f8f8f8;
  font-weight: 600;
}

.about-list {
  padding-top: 10px;
}

.about-list .media {
  padding: 5px 0;
}

.about-list label {
  color: #f9f9f9;
  width: 88px;
  margin: 0;
  font-weight: 600;
  position: relative;
}

.about-list label:after {
  content: "";
  opacity: .5;
  background: #f4f4f4;
  width: 1px;
  height: 12px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 11px;
  transform: rotate(15deg);
}

.about-list p {
  margin: 0;
  font-size: 15px;
}

@media (width <= 991px) {
  .about-avatar {
    margin-top: 30px;
  }
}

.about-section .counter {
  background: #fff;
  border-radius: 10px;
  padding: 22px 20px;
  box-shadow: 0 0 30px #1f2d3d20;
}

.about-section .counter .count-data {
  margin-top: 10px;
  margin-bottom: 10px;
}

.about-section .counter .count {
  color: #e0e0e0;
  margin: 0 0 5px;
  font-weight: 700;
}

.about-section .counter p {
  margin: 0;
  font-weight: 600;
}

mark {
  color: currentColor;
  background-color: #0000;
  background-image: linear-gradient(#fc535699, #fc535699);
  background-position: 0 bottom;
  background-repeat: no-repeat;
  background-size: 100% 3px;
  padding: 0;
}

.theme-color {
  color: #e4e4e4;
}

.dark-color, p {
  color: #fff;
}

.api {
  width: 100%;
  margin-top: 20px;
}

.api iframe {
  border: 2px solid #403e3e;
  border-radius: 10px;
  width: 100%;
  height: 25vh;
  margin-top: 15px;
  box-shadow: 0 0 40px #9593931a;
}
